export const LogoUpload = async (payload) => {
  const formData = new FormData();
  formData.append("file", payload.file);
  formData.append("organizationName", payload.organizationName);

  setAuthenticationToken(localStorage.getItem("token"));

  try {
    const { data } = await axiosInstance.post(`organization/upload`, formData, {
      headers: {
        "Content-Type": "multipart/form-data", // Ensure proper content type for FormData
      },
    });
    return data;
  } catch (error) {
    console.error("Error uploading file:", error);
    throw error;
  }
};

export const LogoUploadPatch = async (payload) => {
  debugger;
  setAuthenticationToken(localStorage.getItem("token"));
  const { data } = await axiosInstance.patch(
    `organization/${localStorage.getItem("organizationid")}`,
    {
      logo: payload,
    }
  );
  return data;
};

export const AcknowledgeMail = async (payload) => {
  debugger;
  setAuthenticationToken(payload.token);
  const { data } = await axiosInstance.patch(
    `alert/update-alert/${payload.id}`,
    {
      status: payload.status,
    }
  );
  return data;
};
